import { Menu } from 'antd';
import { Link } from 'react-router-dom';
import { ROUTERS } from '../../constants/router';

const { SubMenu } = Menu;
export default function MenuAdminVertical(props) {
    const { defaultKey, openKey } = props
    return (
        <Menu selectedKeys={[defaultKey]} mode="inline" defaultOpenKeys={[openKey]}>
            <SubMenu key="shoe" title="Qun L Giy">
                <Menu.Item key="readshoe"><Link to={ROUTERS.READ_PRODUCT}>Xem/Sa Giy</Link></Menu.Item>
                <Menu.Item key="addshoe"><Link to={ROUTERS.ADD_PRODUCT}>Thm Giy</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="color" title="Qun L Mu">
                <Menu.Item key="readcolor"><Link to={ROUTERS.READ_COLOR}>Xem/Sa Mu</Link></Menu.Item>
                <Menu.Item key="addcolor"><Link to={ROUTERS.ADD_COLOR}>Thm Mu</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="size" title="Qun L Size">
                <Menu.Item key="readsize"><Link to={ROUTERS.READ_SIZE}>Xem/Sa Size</Link></Menu.Item>
                <Menu.Item key="addsize"><Link to={ROUTERS.ADD_SIZE}>Thm Size</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="type" title="Qun L Kiu Giy">
                <Menu.Item key="readtype"><Link to={ROUTERS.READ_TYPE}>Xem/Sa Kiu Giy</Link></Menu.Item>
                <Menu.Item key="addtype"><Link to={ROUTERS.ADD_TYPE}>Thm Kiu Giy</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="sales" title="Qun L Khuyn Mi">
                <Menu.Item key="readsale"><Link to={ROUTERS.READ_SALE}>Xem/Sa Khuyn Mi</Link></Menu.Item>
                <Menu.Item key="addsale"><Link to={ROUTERS.ADD_SALE}>Thm Khuyn Mi</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="banner" title="Qun L Banner">
                <Menu.Item key="readbanner"><Link to={ROUTERS.READ_BANNER}>Xem/Sa Banner</Link></Menu.Item>
                <Menu.Item key="addbanner"><Link to={ROUTERS.ADD_BANNER}>Thm Banner</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="news" title="Qun L Tin Tc">
                <Menu.Item key="readnews"><Link to={ROUTERS.READ_NEWS}>Xem/Sa Tin Tc</Link></Menu.Item>
                <Menu.Item key="addnews"><Link to={ROUTERS.ADD_NEWS}>Thm Tin Tc</Link></Menu.Item>
            </SubMenu>
            <Menu.Item key="order"><Link to={ROUTERS.READ_ORDER}>Qun L Ha n</Link></Menu.Item>
            <Menu.Item key="user"><Link to={ROUTERS.READ_USER}>Qun L Ngi Dng</Link></Menu.Item>
            <Menu.Item key="statistic"><Link to={ROUTERS.STATISTIC}>Qun L Thng K</Link></Menu.Item>
        </Menu>
    )
}