//REACT REDUX
import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getShoeDetailAction, removeShoeDetailAction, getGalleryAction, deleteShoeDetailAction, deleteShoeGalleryAction, updateQty } from '../../../../../redux/actions';
//CSS
import {  DeleteTwoTone } from '@ant-design/icons';
import { Table, Space, Row, Col, Image, Breadcrumb } from 'antd';
import swal from "sweetalert";

//ROUTER & HISTORY
import { Link } from 'react-router-dom';
import { ROUTERS } from '../../../../../constants/router';

//Components
import QUANTY from './UpdateQTY';

export default function AdminDetailProduct(props) {
    const { Column } = Table;
    const { match } = props
    const dispatch = useDispatch();
    const getShoeDetail = useSelector(state => state.productReducer);
    const { shoeDetail, gallery } = getShoeDetail;
    useEffect(() => {
        dispatch(getGalleryAction(match.params.id))
        dispatch(getShoeDetailAction(match.params.id));
        return () => {
            dispatch(removeShoeDetailAction);
        }
    }, []);
    function renderShoeColor(id) {
        let newShoeColor = shoeDetail.filter(item => item.id_color === id)
        return (
            newShoeColor[0]?.color
        )
    }
    function renderShoeSize(id) {
        let newShoeSize = shoeDetail.filter(item => item.id_size === id)
        return (
            newShoeSize[0]?.sosize
        )
    }
    
    function UPDATEQTY(value,id_chitietgiay){
        dispatch(updateQty(value.soluong,id_chitietgiay))
    }
    function deleteShoeDetail(id_chitietgiay, id_giay, id_color){
        swal({
            title: "Bn C Chc Chn Khng?",
            text: "Mt khi xa, bn s khng th khi phc li file ny",
            icon: "warning",
            buttons: true,
            dangerMode: true,
          })
          .then((willDelete) => {
            if (willDelete) {
              swal("Xa Thnh Cng", {
                icon: "success",
              });
              dispatch(deleteShoeDetailAction(id_chitietgiay, id_giay, id_color))
            } else {
              swal("D Liu Ca Bn  c Bo Ton!");
            }
          });
    }

    function deleteShoeGallery(id_gallery){
        swal({
            title: "Bn C Chc Chn Khng?",
            text: "Mt khi xa, bn s khng th khi phc li file ny",
            icon: "warning",
            buttons: true,
            dangerMode: true,
          })
          .then((willDelete) => {
            if (willDelete) {
              swal("Xa Thnh Cng", {
                icon: "success",
              });
              dispatch(deleteShoeGalleryAction(id_gallery))
            } else {
              swal("D Liu Ca Bn  c Bo Ton!");
            }
          });
    }
    return (
        <>
            <Breadcrumb style={{ marginTop: '20px', marginLeft: '20px' }}>
                <Breadcrumb.Item><Link to={ROUTERS.READ_PRODUCT}>Xem Giy</Link></Breadcrumb.Item>
                <Breadcrumb.Item>Chi Tit Giy</Breadcrumb.Item>
            </Breadcrumb>
            <h4 style={{ textAlign: 'center' }}>Chi Tit Giy</h4>
            <Row style={{ marginTop: '30px' }}>
                <Col span={22} offset={1}>
                    <Table dataSource={shoeDetail} pagination={{ position: ['none'] }}>
                        <Column
                            title="Tn M Giy"
                            key="tenmagiay"
                            render={(record) => (
                                shoeDetail[0]?.tenmagiay
                            )}
                        />
                        <Column
                            title="Mu"
                            key="color"
                            render={(record) => (
                                renderShoeColor(record.id_color)
                            )}
                        />
                        <Column
                            title="Size"
                            key="size"
                            render={(record) => (
                                renderShoeSize(record.id_size)
                            )}
                        />
                        <Column
                            title="S Lng"
                            key="soluong"
                            render={(record) => (
                                    <QUANTY id_color={record.id_color} id_size={record.id_size} id_chitietgiay={record.id_chitietgiay} shoeDetail={shoeDetail} UPDATEQTY={UPDATEQTY}/>
                            )}
                        />
                        <Column
                            title="Action"
                            key="action"
                            render={(text, record) => (
                                <Space size="middle">
                                    <DeleteTwoTone onClick={() => deleteShoeDetail(record.id_chitietgiay,record.id_giay,record.id_color)} twoToneColor='red' style={{ fontSize: '30px' }} />
                                </Space>
                            )}
                        />
                    </Table>
                </Col>
            </Row>
            <h4 style={{ textAlign: 'center' }}>Th Vin nh</h4>
            <Row style={{ marginTop: '30px' }}>
                <Col span={10} offset={7}>
                    <Table dataSource={gallery} pagination={{ position: ['none'] }}>
                        <Column
                            title="Mu"
                            key="color"
                            render={(record) => (
                                record.color
                            )}
                        />
                        <Column
                            title="nh"
                            key="size"
                            render={(record) => (
                                <Image
                                    height={100}
                                    width={150}
                                    src={`/gallery/${record.image}`}
                                />
                            )}
                        />
                        <Column
                            title="Action"
                            key="action"
                            render={(text, record) => (
                                <Space size="middle">
                                    <DeleteTwoTone onClick={() => deleteShoeGallery(record.id_gallery)} twoToneColor='red' style={{ fontSize: '30px' }} />
                                </Space>
                            )}
                        />
                    </Table>
                </Col>
            </Row>
        </>
    )
}