import { Button, Image, Row, Select, Form, Input, Breadcrumb, Spin } from "antd";
import swal from "sweetalert";
import { ROUTERS } from "../../../constants/router";
import { Link } from 'react-router-dom';
//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import { useState, useEffect } from 'react';
import { getBannerAction, removeBannerAction, getNewsAction, removeNewsAction, updateBannerAction, uploadEditBannerAction } from "../../../redux/actions";

//
export default function AdminEditBanner(props) {
    const { match } = props
    const getBannerContent = useSelector(state => state.adminReducer);
    const { bannerList, newsList, loading_banner_edit } = getBannerContent;
    const dispatch = useDispatch();
    const [newsForm] = Form.useForm()

    useEffect(() => {
        dispatch(getBannerAction());
        dispatch(getNewsAction())
        return () => {
            dispatch(removeBannerAction())
            dispatch(removeNewsAction())
        }
    }, []);

    useEffect(() => { newsForm.resetFields(); }, [match.params.id])

    const bannerDetail = bannerList.filter(item => item.id_banner === parseInt(match.params.id))

    const [selectedFile, setSelectedFile] = useState([]);

    function setFile(e) {
        setSelectedFile(e.target.files)
    }
    function editBanner(values) {
        if (selectedFile.length === 0) {
            const newValues = {
                ...values,
                id_banner: match.params.id,
            }
            dispatch(updateBannerAction(bannerDetail[0]?.url_banner, newValues))
        }
        else {
            const newValues = {
                ...values,
                id_banner: match.params.id,
            }
            const types = ['image/png', 'image/jpeg', 'image/gif']
            if (selectedFile[0].size > 5000000) {
                swal("Kch Thc nh Qu Ln", "vui lng chn file nh c kch thc nh hn 5mb", "warning")
            }
            if (types.every(type => selectedFile[0].type !== type)) {
                swal("Sai nh Dng", "vui lng chn file nh theo nh dng PNG,JPG,JPEG", "warning")
            }
            else {
                const formData = new FormData();
                formData.append("file", selectedFile[0]);
                formData.append("upload_preset", "tccvzctk");
                dispatch(uploadEditBannerAction(formData, newValues));
            }
        }
    }
    return (
        <Row>
            <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                <h4 style={{ marginTop: '30px' }}>Sa Banner</h4>
                <Breadcrumb style={{ marginTop: '20px', marginBottom: '20px', textAlign: 'left', marginLeft: '20px' }}>
                    <Breadcrumb.Item><Link to={ROUTERS.READ_BANNER}>Xem Banner</Link></Breadcrumb.Item>
                    <Breadcrumb.Item>Cp Nht Banner</Breadcrumb.Item>
                </Breadcrumb>
            </div>

            <div className="content3">

                <div className="profile-holder">
                    <div class="form-group files">
                        <Image width="200px" src={bannerDetail[0]?.url_banner} />
                        <p>nh Ba</p>
                        <input type="file" className="form-control" onChange={setFile} accept="image/*" />
                    </div>
                    <Form
                        onFinish={(values) => editBanner(values)}
                        layout="vertical"
                        initialValues={
                            {
                                tieude: bannerDetail[0]?.tieude,
                                title_banner: bannerDetail[0]?.title_banner,
                                sub_title_banner: bannerDetail[0]?.sub_title_banner,
                            }
                        }
                    >
                        <Form.Item
                            label="Tin Tc"
                            name="id_tintuc"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn tin tc...',
                                },
                            ]}
                        >
                            <Select defaultValue={bannerDetail[0]?.tieude}>
                                {newsList.map((item, index) => {
                                    return <Select.Option key={index} value={item.id_tintuc}>{item.tieude}</Select.Option>
                                })}
                            </Select>
                        </Form.Item>
                        <Form.Item
                            label="Tiu  nh Ba"
                            name="title_banner"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tiu  nh ba!',
                                },
                            ]}
                        >
                            <Input.TextArea showCount maxLength={20} />
                        </Form.Item>
                        <Form.Item
                            label="Tiu  Ph nh Ba"
                            name="sub_title_banner"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tiu  ph ca nh ba!',
                                },
                            ]}
                        >
                            <Input.TextArea showCount maxLength={20} />
                        </Form.Item>
                        <Form.Item
                            label="Hiu ng Tiu "
                            name="title_effect"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn hiu ng...',
                                },
                            ]}
                        >
                            <Select defaultValue={bannerDetail[0]?.title_effect}>
                                <Select.Option value="fadeInUp">M dn ln</Select.Option>
                                <Select.Option value="rollIn">Cun vo</Select.Option>
                                <Select.Option value="rotateInDownLeft">Xoay xung bn tri</Select.Option>
                            </Select>
                        </Form.Item>
                        <Form.Item
                            label="Hiu ng Tiu  Ph"
                            name="sub_title_effect"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn hiu ng...',
                                },
                            ]}
                        >
                            <Select defaultValue={bannerDetail[0]?.sub_effect}>
                                <Select.Option value="fadeInDown">M dn xung</Select.Option>
                                <Select.Option value="lightSpeedIn">Lt nhanh vo</Select.Option>
                                <Select.Option value="rotateInUpRight">Xoay ln bn phi</Select.Option>
                            </Select>
                        </Form.Item>
                        <Form.Item
                            label="Hiu ng Button"
                            name="button_effect"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn hiu ng...',
                                },
                            ]}
                        >
                            <Select defaultValue={bannerDetail[0]?.button_effect}>
                                <Select.Option value="zoomIn">Zoom dn ln</Select.Option>
                                <Select.Option value="slideInUp">Trt ln</Select.Option>
                                <Select.Option value="rotateIn">Xoay mt vng</Select.Option>
                            </Select>
                        </Form.Item>
                        {loading_banner_edit ? <Spin size="large" /> :
                            (
                                <Form.Item>
                                    <Button type='primary' htmlType="submit">Sa nh Ba</Button>
                                </Form.Item>
                            )}
                    </Form>
                </div>
            </div>
        </Row>
    )
}