import React, { useEffect } from 'react'
import { useSelector, useDispatch } from "react-redux";
//CSS
import '../LogInOut/styles.css'
import { Form, Input, Button, Tabs, Select } from 'antd';
//Components
import { registerAction, loginAction, logoutAction, removeCityAction, getCityAction, getDistrictAction, getWardAction, removeWardAction, removeDistrictAction } from '../../redux/actions';
import { ROUTERS } from '../../constants/router';
import { Link } from 'react-router-dom';
function LogInOut() {
    //REDUX 
    const dispatch = useDispatch();
    const user = useSelector(state => state.userReducer);
    const { userInfo, cityList, districtList, wardList } = user;
    //STATE && REF
    //Form Layout & FORM LINH TINH
    const layout = {
        wrapperCol: {
            span: 10,
        },
    };
    const tailLayout = {
        wrapperCol: {

        },
    };
    const { TabPane } = Tabs;

    const onFinishReg = (values) => {
        const xaPhuong = wardList.filter(item => item.id === values.xa_phuong);
        const quanHuyen = districtList.filter(item => item.id === values.quan_huyen);
        const tinhThanh = cityList.filter(item => item.id === values.tinh_thanh);
        const diachi = values.sonha + ', ' + xaPhuong[0]?._name + ', ' + quanHuyen[0]?._name + ', ' + tinhThanh[0]?._name;
        const newValues = {
            ...values,
            diachi,
        }
        dispatch(registerAction(newValues))
    }
    const onFinishLog = (values) => {
        dispatch(loginAction(values))
    };

    useEffect(() => {
        dispatch(getCityAction());
        return () => {
            dispatch(removeCityAction());
            dispatch(removeDistrictAction());
            dispatch(removeWardAction());
        }
    }, []);

    const [newsForm] = Form.useForm()

    return (
        <div className="form container">
            {userInfo.length > 0
                ? <div>
                    <h1>Xin cho {userInfo[0]?.username}</h1>
                    <Button onClick={() => dispatch(logoutAction())}>Log Out</Button>
                </div>
                : <Tabs defaultActiveKey="1">
                    <TabPane tab="ng Nhp" key="1">
                        <Form
                            {...layout}
                            name="basic"
                            type="flex"
                            justify="center"
                            align="middle"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={onFinishLog}
                        >
                            <Form.Item
                                className="form-in"
                                name="usernameLog"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your username!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp email/ tn ti khon..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="passwordLog"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your password!',
                                    },
                                ]}
                            >
                                <Input.Password placeholder="Nhp mt khu..." />
                            </Form.Item>
                            <Link to={ROUTERS.FORGOT_PASSWORD}>Qun Mt Khu?</Link>
                            <Form.Item {...tailLayout}>
                                <Button className="btn-signin" type="primary" htmlType="submit">
                                    ng Nhp
                                </Button>
                            </Form.Item>
                        </Form>
                    </TabPane>
                    <TabPane tab="ng K" key="2">
                        <Form
                            {...layout}
                            form={newsForm}
                            type="flex"
                            justify="center"
                            align="middle"
                            name="basicReg"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={onFinishReg}
                            scrollToFirstError
                        >
                            <Form.Item
                                className="form-in"
                                name="hoten"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng in h tn vo khung',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp h tn ca bn..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="username"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp tn ti khon bn mun to',
                                    },
                                    {
                                        min: 6,
                                        message: 'Ti khon phi c  di t nht 6 k t'
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp tn ti khon..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="email"
                                rules={[
                                    {
                                        type: 'email',
                                        message: 'Email bn nhp khng hp l!',
                                    },
                                    {
                                        required: true,
                                        message: 'Vui lng nhp Email!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp email ca bn..." />
                            </Form.Item>

                            <Form.Item
                                name="password"
                                className="form-in"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp mt khu!',
                                    },
                                    {
                                        min: 8,
                                        message: 'Mt khu ca bn phi di t nht 8 k t!'
                                    },
                                    {
                                        whitespace: false,
                                        message: 'Vui lng khng dng khong trng  t mt khu!'
                                    },
                                ]}
                                hasFeedback
                            >
                                <Input.Password placeholder="Nhp mt khu..." />
                            </Form.Item>

                            <Form.Item
                                name="confirm"
                                className="form-in"
                                dependencies={['password']}
                                hasFeedback
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp mt khu',
                                    },
                                    {
                                        whitespace: false,
                                        message: 'Vui lng khng dng khong trng  t mt khu!'
                                    },
                                    ({ getFieldValue }) => ({
                                        validator(_, value) {
                                            if (!value || getFieldValue('password') === value) {
                                                return Promise.resolve();
                                            }
                                            return Promise.reject(new Error('Mt khu bn va nhp khng khp vi mt khu trc !'));
                                        },
                                    }),
                                ]}
                            >
                                <Input.Password placeholder="Xc nhn mt khu..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="sodienthoai"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp s in thoi!'
                                    },
                                    {
                                        min: 9,
                                        message: 'S in thoi bn va nhp khng ng nh dng'

                                    },
                                    {
                                        required: true,
                                        pattern: new RegExp("^[0-9]*$"),
                                        message: "S in thoi ch bao gm s, khng bao gm k t khc!"
                                    }
                                ]}
                            >
                                <Input placeholder="Nhp s in thoi" addonBefore="+84" style={{ width: '100%' }} minLength="9" maxlength="10" />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="tinh_thanh"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp a ch!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="Tnh/Thnh Ph"
                                    showSearch
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    onChange={(value) => {
                                        dispatch(getDistrictAction(value));
                                        newsForm.setFieldsValue({
                                            quan_huyen: undefined,
                                            xa_phuong: undefined,
                                        })
                                    }}
                                    style={{ textAlign: 'left' }}
                                    allowClear
                                >
                                    {cityList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>
                            <Form.Item
                                className="form-in"
                                name="quan_huyen"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp a ch!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="Qun/Huyn"
                                    showSearch
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    onChange={(value) => {
                                        dispatch(getWardAction(value))
                                        newsForm.setFieldsValue({
                                            xa_phuong: undefined,
                                        })
                                    }}
                                    style={{ textAlign: 'left' }}
                                    allowClear
                                >
                                    {districtList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>


                            <Form.Item
                                className="form-in"
                                name="xa_phuong"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp a ch!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="X/Phng"
                                    showSearch
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    style={{ textAlign: 'left' }}
                                    allowClear
                                >
                                    {wardList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>
                            <Form.Item
                                className="form-in"
                                name="sonha"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp a ch!',
                                    },
                                ]}
                            >
                                <Input placeholder="Ta Nh, Tn ng..." />
                            </Form.Item>

                            <Form.Item {...tailLayout}>
                                <Button className="btn-signin" type="primary" htmlType="submit">ng K</Button>
                            </Form.Item>
                        </Form>
                    </TabPane>
                </Tabs>
            }

        </div>
    )
}

export default LogInOut
